
.buttons-keyboard {
	      margin: 1.2%; 
          background: var(--background_buttons);
          border-radius: 10px 10px 10px 10px;
          outline: none;
          border: none; 
          width: 30%;  
          height: 8.5vh;
          box-shadow: var(--box-shadow_buttons);
          position: relative;
          transition: all ease 0.3s;
          font-size: calc(3.4vh + 2.8vw); 
		  color: var(--color_buttons);
		  max-height: 65px;
	  	  }
	
	
.buttons-keyboard:active { box-shadow: var(--box-shadow_buttonsActive);
                           top: 3px;}
				  
.wrapper-buttons { bottom: 55px;
                   position: fixed;
		           width: 100%; 
		           text-align: center;
                   padding-bottom: var(--safe-area-inset-bottom);
                   }

.region-code { text-align: center;
               font-size: calc(0.6rem + 1.3vh + 1vw);
			   bottom: 78.5%;
			   position: fixed;
			   width: 90%;
			   left:5%;}

.input-wrapper { background: var(--background_input-wrapper);
                 width: 100%;				 
                 border-radius: 15px 15px 15px 15px;
                 position: absolute;                
                 height: 100% ; 
				 display: flex; 
				 justify-content: center;
				 align-items: center;
				 padding-left: env(safe-area-inset-left);
                 padding-right: env(safe-area-inset-right);
				 }

.Input { background-color: rgba(0,0,0,0);       
        width: 100%; 
        font-size: calc(1rem + 10vh );
		border: none; 
		outline: none; 
		text-align: center;
		color: var(--color_Input);
		padding-left: env(safe-area-inset-left);
		padding-right: env(safe-area-inset-right);
		}

.wrapper-input-wrapper { background: rgba(0,0,0,0);
                         width: 56%;
				         left: 17%;
                         border-radius: 15px 15px 15px 15px;
                         position: fixed;
                         bottom: 55%;
                         height: 20%;  
						 max-height: 145px;
						 padding-left: env(safe-area-inset-left);
						 padding-right: env(safe-area-inset-right);
						 }	

.GoToMapOff { background: var(--background_GoToMap);
            position: fixed;
           bottom: 55%;
		   height: 20%;
		   left: 67%;
		   border: none;
		   outline: none;
		   border-radius: 0px 10px 10px 0px;
		   width: 17%;
		   max-height: 145px
		  }

.GoToMapOn { background: var(--background_GoToMap);
            position: fixed;
           bottom: 55%;
		   height: 20%;
		   left: 67%;
		   border: none;
		   outline: none;
		   border-radius: 0px 10px 10px 0px;
		   width: 17%;
		   max-height: 145px
		  }

.buttonMapOff { color: #929292; position: absolute;
  top: 0;
  left: 33%;
  bottom: 0;
  right: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%; }

.buttonMapOn { color: #E10C0C; position: absolute;
  top: 0;
  left: 33%;
  bottom: 0;
  right: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;}

.ONLYBOXSHADOW { position: absolute; 
                 bottom: 55%;
				 width: 67%; 
				 height: 20%;
				 background: rgba(0,0,0,0); 
				 left: 17%; 
				 border-radius: 15px 15px 15px 15px; 
				 box-shadow:  var(--ONLYBOXSHADOW);
				 max-height: 145px;
				  }

@media screen and (min-width: 1200px) {
.buttons-keyboard {
     font-size: 55px;
	 width: 20%;
     height: 7vh;
     max-height: 500px;	 
  }
 
.wrapper-input-wrapper { 
                         width: 30%;
				         left: 31%;
                         max-height: 500px;					 
						 } 
 
 .GoToMapOn { 
		   left: 57%;	  
		   width: 12vw;	
           max-height: 500px;			   
		   }
.GoToMapOff { 
		   left: 57%;	  
		   width: 12vw;	
           max-height: 500px;		   
		   }
		   
.ONLYBOXSHADOW { width: 38%;
				 left: 31%; 
				 max-height: 500px;
                }				 
}

:root {
  --background_buttons: white;  
  --color_buttons: black;
  --box-shadow_buttons: 2px 4px 2px #C0C0C0;
  --box-shadow_buttonsActive: 0 3px 0 #DCDCDC;
  --background_input-wrapper: white;
  --color_Input: black;
  --background_GoToMap: #F6F6F6;
  --ONLYBOXSHADOW: 0px 0px 15px 1px  #D9D9D9;
  
		  }

body[scheme="client_dark"] {
  --background_buttons: #2C2C2C;
  --color_buttons: #C9C9C9;    
  --box-shadow_buttons: 2px 4px 2px #3E3E3E;  
  --box-shadow_buttonsActive: 0 3px 0 #2E2E2E;	
  --background_input-wrapper: #2C2C2C; 
  --color_Input: #C9C9C9;  
  --background_GoToMap: #373737;
  --ONLYBOXSHADOW: 0px 0px 15px 0px  #4D4D4D;
			   }
			   [scheme="space_gray"] {
  --background_buttons: #2C2C2C;
  --color_buttons: #C9C9C9;    
  --box-shadow_buttons: 2px 4px 2px #3E3E3E;  
  --box-shadow_buttonsActive: 0 3px 0 #2E2E2E;	
  --background_input-wrapper: #2C2C2C; 
  --color_Input: #C9C9C9;  
  --background_GoToMap: #373737;
  --ONLYBOXSHADOW: 0px 0px 15px 0px  #4D4D4D;
			   }


